  .table-wrapper__cell--red {
    background-color: #FFF1F8;
  }
  .table-wrapper__cell--check::before {
    content: "";
    width: 7px;
    height: 1px;
    background-color: #FF56A9;
    position: absolute;
    transform: rotate(40deg);
    left: 48%;
  }
  .table-wrapper__cell--check::after {
    content: "";
    width: 11px;
    height: 1px;
    background-color: #FF56A9;
    position: absolute;
    transform: rotate(-50deg);
    top: 47%;
    left: 50%;
  }
  .table-wrapper__cell--purple.table-wrapper__cell--check::after,
  .table-wrapper__cell--purple.table-wrapper__cell--check::before {
    background-color: #9570EE;
  }
}
